<template>
    <div class="public_content">
        <div class="public_title">
          <span>租房录入房源</span>
          <el-button class="fr" icon="el-icon-back" size="small" @click='$router.push({name: "rentlists"})'>房源库</el-button>
        </div> 
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">



            <el-form-item label="小区名称" prop="community_id">
              <el-select
                @blur='blurMethod'
                style="width: 250px;margin-right:20px;"
                v-model="ruleForm.community_id"
                filterable
                remote
                reserve-keyword
                placeholder="请输入关键词"
                :remote-method="remoteMethod"
                :loading="loading"> 
                <el-option
                  v-for="item in community_id"
                  :key="item.id"
                  :label="item.communityname"
                  :value="item.id">
                </el-option>
              </el-select>  
                <el-button type="primary" round v-show="community_id.length == 0 && temp" @click="addCommunity">录入小区</el-button>
            </el-form-item>
 


            <el-form-item label="房屋情况" prop="housetype" class="inlineBlock">
                <el-select v-model="ruleForm.housetype" placeholder="房屋类型">
                    <el-option
                      v-for="item in housetype"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>

            <el-form-item prop="fixture" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.fixture" placeholder="装修程度">
                    <el-option
                      v-for="item in decoration"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select> 
            </el-form-item>
            <el-form-item prop="towards" label-width="0" class="inlineBlock">
                <el-select v-model="ruleForm.towards" placeholder="房屋朝向">
                    <el-option
                      v-for="item in aspect"
                      :key="item.id"
                      :label="item.paramname"
                      :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>

          <div>
            <el-form-item label="户型面积" prop="bedroom" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.bedroom"><span slot="suffix">室</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="livingroon" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.livingroon"><span slot="suffix">厅</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="bathroom" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.bathroom"><span slot="suffix">卫</span></el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="area" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.area"><span slot="suffix">㎡</span></el-input>
            </el-form-item>

            	<el-form-item class="inlineBlock" label-width="0" prop="is_whole" style="margin-left: 10px">
            	   <el-radio-group v-model="ruleForm.is_whole" >
                    <el-radio :label="1">整租</el-radio>
                    <el-radio :label="2">合租</el-radio>
                  </el-radio-group>
         	    </el-form-item>
          </div>

         <el-form-item label="房屋租金" prop="price" class="inlineBlock">
            	<el-input v-model="ruleForm.price" placeholder="0表示面议" style="width: 200px"><span slot="suffix">元/月</span></el-input>
           </el-form-item>

           <el-form-item prop="pricetype" label-width="0" class="inlineBlock">
           	  <el-select  placeholder="付款方式" v-model="ruleForm.pricetype">
            	<el-option
             	 	v-for="item in paytype"
              		:key="item.id"
              		:label="item.paramname"
              		:value="item.id">
            	</el-option>
          	  </el-select>
           </el-form-item>
          
            <el-form-item label="房屋标题" prop="title">
                <el-input style="width:300px;" v-model="houseTitle"></el-input>
            </el-form-item>

           

            <div>

            
             <el-form-item label="房屋楼层" prop="floor" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.floor">
                  <span slot="prefix">第</span>
                  <span slot="suffix">层</span>
                </el-input>
             </el-form-item>
             <el-form-item label-width="0" prop="totalfloor" class="inlineBlock">
                <el-input style="width:100px;" v-model="ruleForm.totalfloor">
                  <span slot="prefix">共</span>
                  <span slot="suffix">层</span>
                </el-input>
            </el-form-item>
            </div> 

           <el-form-item label="封面图片" prop="thumb"> 
               
              <upload-img  v-if="dataSuccess" :data='ruleForm.thumb' @onSuccess="changeLogo($event,'thumb')" style="display: inline-block"></upload-img>

              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>单图，供列表页使用</span>

               
            </el-form-item>

            <el-form-item label="房源图片" prop="images">
               <upload-imgs  v-if="dataSuccess" :data='ruleForm.images' @onSuccess="changeImages"></upload-imgs>
                 
            </el-form-item>  

            <!-- 上传视频 -->
            <el-form-item label="上传视频" prop="video">
              <upload-video @onSuccess="changeVideo"></upload-video>

            </el-form-item> 


            <el-form-item label="VR看房" prop="panorama">
              <el-input v-model="ruleForm.panorama" style="width: 300px">
                    <template slot="prepend">Http://</template>
              </el-input>
              <span class="public_littleTip"><i class="iconfont">&#xe670;</i>请输入VR文件的URL</span>

            </el-form-item>


            <el-form-item label="房屋配置" prop="matching">
                 <el-checkbox v-model="checkAll" @change="handleCheckAllChange" style="display: inline-block">全选</el-checkbox>

                <el-checkbox-group v-model="ruleForm.matching" @change="handleCheckedListsChange" style="display: inline-block;margin-left: 30px;">
                    <el-checkbox v-for="item in matching" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox> 
                </el-checkbox-group>
            </el-form-item> 

            <el-form-item label="特色标签" prop="tag">
                <el-checkbox-group v-model="ruleForm.tag">
                    <el-checkbox v-for="item in tag" :label="item.id" :key="item.id">
                        {{ item.paramname }}
                    </el-checkbox>
                </el-checkbox-group>
            </el-form-item> 

            <el-form-item label="房屋描述" prop="description">
              <editor :value="ruleForm.description" 
                       @show="editors">
              </editor>  
            </el-form-item>
             <el-form-item label="业主心态" prop="mentality" style="margin-bottom: 120px;">
              <el-input style="width: 700px" type="textarea" :rows="6"  v-model="ruleForm.mentality">
              </el-input>
            </el-form-item>

            <el-button type="primary" round @click="submitForm('ruleForm')" class="public_submit">确认提交</el-button>
         </el-form>
    </div>
</template>

<script>
import { rentPublish, getCommunity } from "@/api/house";
import uploadImg from "@/components/upload/uploadimg";
import uploadImgs from "@/components/upload/uploadimgs";
import uploadVideo from "@/components/upload/uploadVideo";
import editor from "@/components/tinymce/editor";

export default {
  components: {
    uploadImg,
    uploadImgs,
    uploadVideo,
    editor
  },
  data() {
    return {
      dataSuccess: false,
      checkAll: false,
      loading: false,
      temp: "", // 临时存储 
      checkAll: false,
      community_id: [], // 小区列表
      housetype: [], // 房屋类型
      decoration: [], // 装修程度
      aspect: [], // 朝向
      paytype: [], // 付款方式
      tag: [], // 特色标签
      matching: [], // 房屋配置
      ruleForm: {
        community_id: "",
        housetype: "",
        fixture: "",
        towards: "",
        bedroom: "",
        livingroon: "",
        bathroom: "",
        area: "",
        title: "",
        floor: "",
        totalfloor: "",
        video: "",
        panorama: "",
        price: "",
        tag: [],
        matching: [],
        pricetype: "",
        description: "",
        mentality: "",
        is_whole: ""
      },
      rules: {
        community_id: [
          { required: true, message: "请输入小区名称", trigger: "blur" }
        ],
        housetype: [{ required: true, message: "请选择", trigger: "change" }],
        fixture: [{ required: true, message: "请选择", trigger: "change" }],
        towards: [{ required: true, message: "请选择", trigger: "change" }],
        bedroom: [{ required: true, message: "请输入", trigger: "blur" }],
        livingroon: [{ required: true, message: "请输入", trigger: "blur" }],
        bathroom: [{ required: true, message: "请输入", trigger: "blur" }],
        area: [{ required: true, message: "请输入", trigger: "blur" }],
        is_whole: [{ required: true, message: "请选择", trigger: "change" }],
        price: [{ required: true, message: "请输入金额", trigger: "blur" }],
        pricetype: [
          { required: true, message: "请选择付款方式", trigger: "change" }
        ],
        title: [{ required: true, message: "请输入标题", trigger: "blur" }]
      }
    };
  },

  created() {
    if (this.$route.params.search) {
      this.remoteMethod(this.$route.params.search);
    }

    if (this.$route.query.id) {
      rentPublish("get", this.$route.query.id).then(res => {
        if (res.data.code == 1) {
          let data = res.data.data.agent;
          this.remoteMethod(data.communityname).then(res => {
            this.ruleForm = data;

            this.ruleForm.matching = data.matching.map(item => {
              return Number(item);
            });

            if (this.ruleForm.matching.length == this.matching.length) {
              this.checkAll = true;
            }

            this.ruleForm.tag = data.tag.map(item => {
              return Number(item);
            });
            this.dataSuccess = true;
          });
        }
      });
    } else {
      this.dataSuccess = true;
    }

    rentPublish("get").then(res => {
      this.matching = res.data.data.matching ? res.data.data.matching : [];
      this.tag = res.data.data.tag;
      this.paytype = res.data.data.paytype;
      this.decoration = res.data.data.decoration;
      this.aspect = res.data.data.aspect;
      this.housetype = res.data.data.housetype;
    });
  },

  computed: {
    fixture() {
      let r = "";
      if (this.ruleForm.fixture) {
        this.decoration.forEach(element => {
          if (element.id == this.ruleForm.fixture) {
            r = element.paramname;
          }
        });
        return r;
      } else {
        return "";
      }
    },
    houseName() {
      let r = "";
      if (this.ruleForm.community_id) {
        this.community_id.forEach(element => {
          if (element.id == this.ruleForm.community_id) {
            r = element.communityname;
          }
        });
        return r;
      } else {
        return "";
      }
    },
    houseTitle: {
      set: function(value) {
        this.ruleForm.title = value;
      },
      get: function() {
        if (!this.$route.query.id) {
          return (
            this.houseName +
            " " +
            this.ruleForm.bedroom +
            (this.ruleForm.bedroom ? "室" : "") +
            this.ruleForm.livingroon +
            (this.ruleForm.livingroon ? "厅" : "") +
            this.ruleForm.bathroom +
            (this.ruleForm.bathroom ? "卫" : "") +
            " " +
            this.ruleForm.area +
            (this.ruleForm.area ? "平米" : "") +
            " " +
            this.fixture +
            " " +
            this.ruleForm.price +
            (this.ruleForm.price ? "元/月" : "")
          );
        } else {
          return this.ruleForm.title;
        }
      }
    }
  },
  methods: {
    /* 搜索小区 */
    remoteMethod(query) {
      return new Promise((resolve, reject) => {
        this.temp = query;
        if (query !== "") {
          this.loading = true;
          getCommunity(query)
            .then(res => {
              this.community_id = res.data.data_like;
              if (this.$route.params.search) {
                this.ruleForm.community_id = this.community_id[0].id;
              }
              resolve(res);
            })
            .catch(error => {
              reject(error);
            });

          this.loading = false;
        } else {
          this.community_id = [];
          resolve();
        }
      });
    },
    blurMethod(e) {
      if (this.community_id.length == 0) {
        this.ruleForm.community_id = this.temp;
      }
    },

    /* 添加小区 */
    addCommunity() {
      /* 1 esf  2 rent 3 store */
      this.$router.push({
        name: "addcommunity",
        params: { type: 2, search: this.temp }
      });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.title = this.houseTitle;
          rentPublish("post", "", this.ruleForm).then(res => {
            if (res.data.code == 1) {
              if (this.$route.query.id) {
                this.$message.success("编辑成功");
              } else {
                this.$message.success("新增成功");
              }

              this.$router.push({ name: "rentlists" });
            }
          });
        } else {
          return false;
        }
      });
    },
    handleCheckAllChange(val) {
      if (val) {
        this.ruleForm.matching = this.matching.map(item => {
          return Number(item.id);
        });
      } else {
        this.ruleForm.matching = [];
      }
    },
    handleCheckedListsChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.matching.length;
    },
    changeLogo(data, key) {
      this.ruleForm[key] = data;
    },
    changeImages(data) {
      this.ruleForm.images = data;
    },
    changeVideo (data) {
      this.ruleForm.video = data;
    },
    editors(content) { 
      this.ruleForm.description = content;
    }, 
  }
};
</script>

<style lang='scss' scoped>
</style>



